<template lang="">
  <form @submit.prevent="fnSetImgArr()">
    <button>추가</button>
    <button v-if="imgArr.length===0?false:true" @click="fnDeleteImgArr()" type="button">삭제</button>
  </form>
</template>
<script>
import { inject } from 'vue';

export default {
  setup(){
    const {imgArr, setImgArr} = inject('provideArr')
    console.log(imgArr.length);

    const fnSetImgArr = function(){
      const copyArr = [...imgArr.value]
      if(copyArr.length>=5) return false
      copyArr.push(copyArr.length+1)
      setImgArr(copyArr)
    }

    const fnDeleteImgArr = function(){
      const copyArr = [...imgArr.value]
      copyArr.pop()
      setImgArr(copyArr) 
    }
    return{
      fnSetImgArr,fnDeleteImgArr,imgArr
    }
  }
}
</script>
